export class ReportPageModel {
    constructor() {
        this.observers = [];
        this.data = null;
    }

    registerObserver(fn) {
        this.observers.push(fn)
    }

    notify() {
        this.observers.forEach(observer => observer.observe(this));
    }

    setTasks(tasks) {
        this.globalData = []; // array of objects
        this.tasks = tasks;  // data from firebase [id: task, id: task ...]

        if (tasks) {
            Object.keys(tasks).forEach(id => {
                const task = tasks[id];
                this.addToTaskList(task);
            });
        }
        this.data = this.globalData;
    }

    addToTaskList(task) {
        this.globalData.push(task);
    }

    getPomodorosCount(status) {
        let count = 0;
        this._data.forEach(task => {
            if (task.priority === status) {
                count += +task.estimation
            }
        });
        return count;
    }

    sortTasksData(calendarTab, taskTab) {
        switch (calendarTab) {
            case 'day' :
                this._data = this.globalData.filter(task => {
                    return new Date(task.completeDate).toLocaleDateString() === new Date().toLocaleDateString()
                });

                if (taskTab === 'tasks') {
                    this.data = {
                        today: new Date().toLocaleDateString(),
                        urgent: this._data.filter(task => task.priority === 'urgent').length,
                        high: this._data.filter(task => task.priority === 'high').length,
                        medium: this._data.filter(task => task.priority === 'medium').length,
                        low: this._data.filter(task => task.priority === 'low').length,
                        failed: this._data.filter(task => task.priority === 'failed').length,
                    }
                } else if (taskTab === 'pomodoros') {
                    this.data = {
                        today: new Date().toLocaleDateString(),
                        urgent: this.getPomodorosCount('urgent'),
                        high: this.getPomodorosCount('high'),
                        medium: this.getPomodorosCount('medium'),
                        low: this.getPomodorosCount('low'),
                        failed: this.getPomodorosCount('failed'),
                    }
                }
                break;
            case 'week':
                this._data = [];

                this.globalData.forEach(task => {
                    for (let i = 0; i < 8; i++) {
                        if (new Date(task.completeDate).toLocaleDateString() === new Date(new Date().setDate(new Date().getDate() - i)).toLocaleDateString()) {
                            this._data.push(task)
                        }
                    }
                });
                console.log(this._data);
                break;
            case 'month':
                this._data = [];

                this.globalData.forEach(task => {
                    for (let i = 0; i < 30; i++) {
                        if (new Date(task.completeDate).toLocaleDateString() === new Date(new Date().setDate(new Date().getDate() - i)).toLocaleDateString()) {
                            this._data.push(task)
                        }
                    }
                });
                console.log(this._data);
                break;
        }
        this.notify();
    }
}